
import { defineComponent, ref, unref } from 'vue'
import {
  IonLabel,
  IonInput,
  IonItem,
  IonCard,
  IonCardContent,
  IonCardHeader,
  IonButton,
  IonPage,
  useIonRouter,
} from '@ionic/vue'
import { supabase } from '@/api'
import { notify } from '@/notify'

export default defineComponent({
  name: 'LoginView',
  components: {
    IonLabel,
    IonInput,
    IonItem,
    IonCard,
    IonCardContent,
    IonCardHeader,
    IonButton,
    IonPage,
  },
  setup() {
    const email = ref('')
    const password = ref('')
    const ionRouter = useIonRouter()

    async function login() {
      const { error } = await supabase.auth.signIn({
        email: unref(email),
        password: unref(password),
      })
      if (error) {
        notify.error('Fehler beim Einloggen.', error)
        return
      }
      ionRouter.push('/')
    }

    return {
      email,
      password,
      login,
    }
  },
})
